
img{display:block;width:100%
}
header{overflow:hidden;font-size:0.14rem;color:#444;max-width:720px;padding:0.15rem;margin:0.15rem auto 0
}
header>.btn-return{float:left;padding:0.05rem 0.15rem;background:#ccc;color:#fff;border-radius:4px;letter-spacing:2px
}
header>.email{float:right
}
header>.email a{color:#444;text-decoration:underline
}
article{max-width:720px;padding:0.15rem;margin:auto;letter-spacing:2px;line-height:1.5;font-size:0.15rem
}
article em{font-style:normal;font-weight:600;text-decoration:underline
}
article p{margin-top:0.15rem
}
article h1{font-size:0.24rem;margin-bottom:0.15rem
}
article h1+p{font-size:0.12rem;font-weight:100;color:#555
}
article h2{margin:0.3rem 0 0.05rem 0
}
article h3{margin:0.15rem 0 0.05rem 0
}
article pre{padding:0.1rem;border:1px dashed #ccc
}
.btn-return-bottom{background:#fff;border:1px solid #999;color:#999;border-radius:4px;letter-spacing:2px;margin:0.15rem auto;display:block;text-align:center;width:60%;height:0.3rem;line-height:0.3rem
}



